import {View} from 'react-native';
import {IParty, IRecipient} from '../../interface';
import TextCM from '../../../../../components/Text';
import RecipientScreen from '../recipient';
import styles from './styles';
import {listTypeUserAction} from '../../helper';
import {useTranslation} from 'react-i18next';
import {isEmpty} from 'lodash';

type Props = {
  party: IParty;
  index: number;
  error: {[key: string]: string};
  setError: any;
};

const PartyScreen = ({party, index, error, setError}: Props) => {
  const {t} = useTranslation();
  const listRecipients = listTypeUserAction.map(
    (item: string) => (party.recipient as any)[item],
  );

  return (
    <>
      {!isEmpty(listRecipients?.filter(a => a.length)) && (
        <View>
          <View style={styles.ctnMain}>
            <TextCM style={styles.text}>
              {t('RV.econtract.join')} {index}
            </TextCM>
          </View>
          <View style={styles.ctnRecipient}>
            {listTypeUserAction.map((item: string) => {
              const listRecipients = (party.recipient as any)[
                item
              ] as IRecipient[];
              if (!isEmpty(listRecipients)) {
                return listRecipients?.map((recipient, idx) => (
                  <RecipientScreen
                    error={error}
                    setError={setError}
                    key={idx}
                    recipient={recipient}
                    type={item}
                  />
                ));
              }
            })}
          </View>
        </View>
      )}
    </>
  );
};

export default PartyScreen;
